body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.jsoneditor {
  border: thin solid #999999 !important;
}

.jsoneditor-menu {
  background-color: #999999 !important;
  border-bottom: 1px solid #999999 !important;
}

.str-chat-channel {
  max-height: 75vh !important;
}

.str-chat.messaging {
  background-color: transparent !important;
}

.str-chat-channel.messaging .str-chat__main-panel {
  padding: 0 !important;
}

.str-video {
  height: 75vh;
  background-color: #272a30;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-width: 0;
  max-width: 100%;
  overflow: hidden;
}

.str-video .participants-bar {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
}

.str-video .participant {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.str-video .controls {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 10px;
  border-radius: 5px;
}

.str-video .controls button {
  margin: 0 5px;
  color: #fff;
  background-color: #444;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.str-video .controls button:hover {
  background-color: #666;
}

.str-video .video-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.str-chat.messaging textarea {
  padding: 20px;
}

button.str-chat__header-hamburger {
  display: none;
}